<template>
  <div class="container">
    <div class="col">
      <div class="card-input-container">
        <input
          v-model="search"
          type="text"
          placeholder="Введите название карты"
        />
      </div>
      <template v-if="!selectedCard">
        <div
          class="suggestion"
          v-for="card in filteredCards"
          :key="card.name"
          @click="selectCard(card)"
        >
          {{ card.name }}
        </div>
      </template>

      <AgendaCard v-if="selectedCard" :card="selectedCard" />
    </div>
    <div class="col"><PhaseInfo /></div>
  </div>
</template>

<script>
import AgendaCard from "./components/AgendaCard.vue";
import PhaseInfo from "./components/PhaseInfo.vue";
import cards from "@/assets/agendaCards";
import Fuse from "fuse.js";

const fuse = new Fuse(cards, {
  keys: ["name"],
});

export default {
  name: "App",
  components: {
    AgendaCard,
    PhaseInfo,
  },
  data() {
    return {
      cards,
      selectedCard: null,
      search: "",
    };
  },
  computed: {
    filteredCards() {
      if (!this.search) {
        return cards;
      }

      return fuse.search(this.search).map(({ item }) => item);
    },
  },
  methods: {
    selectCard(card) {
      this.selectedCard = card;
      this.search = "";
    },
  },
};
</script>

